.App {
  //nav
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0 0 50px 0;
  }
}

#gallery {
    line-height:0;
    -webkit-column-count:4; /* split it into 5 columns */
    -webkit-column-gap:5px; /* give it a 5px gap between columns */
    -moz-column-count:4;
    -moz-column-gap:5px;
    column-count: 4;
    column-gap:5px;

    .Photo {
      img {
          width: 100% !important;
          height: auto !important;
          margin-bottom:5px; /* to match column gap */
        &:hover {
          cursor: pointer;
      }
    }
  }
}

.Photo {

  .deleteButton, .updateButton {
    margin: 0 15px 15px 0;
    padding: 10px 24px;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
  }

  .updateButton {
    background-color: grey;
  }

  .deleteButton {
    background-color: red;
  }
}

button {
  &:hover {
    cursor: pointer;
  }
}

